<template>
  <div class="music-list">
     <div class="search">
     	<input v-model='searchKey'><button class="confirm-search" @click='passKey'>搜索</button>
     </div>
     <div class="mlist">
     	<ul>
     		<li v-for='(item,index) in playList'>
        <span class="inedxNumber">{{index}}</span>
        <span class="mlist-name">{{item.songname}}</span>
     		<span class="mlist-singer">{{item.singername}}</span>
        </li>
     	</ul>
     </div>
  </div>
</template>s

<script>
export default {
  name: 'music-list',
  props:{
  	playList:{
      type:Array
    }
  },
  data(){
    return{
      searchKey:'',
      page:1
    }
  },
  methods:{
    passKey(){
      this.$emit('keychange',this.searchKey,this.page);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 .music-list{
 	width: 80%;
 	background: #f5f5f5;
	margin:10px auto;
	box-shadow: 0 1px 8px rgba(0,0,0,0.12);
	border-radius: 8px;
 }
 .search{
 	padding: 5px 10px;
 	width: 100%;
 }
 .search input{
 	border-radius: 5px 0 0 5px;
 	border:1px solid #ccc;
 	border-right: 0;
 	padding: 6px 12px;
 	font-size: 14px;
 	box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
 	outline: none;
 	width: calc(100% - 110px);
 }
 .search input:focus{
 	border-radius: 5px 0 0 5px;
 	border:1px solid #42A6F3;
 }
 .confirm-search{
 	width: 60px;
 	height:30px;
 	vertical-align: top;
 	border-radius:  0 5px 5px 0 ;
 	border:1px solid #ccc;
 	background-color: #fff;
 	outline: none;
 }
 .confirm-search:hover{
 	background-color: #ccc;
 }
 .mlist ul{
 	list-style: none;
    margin: 0;
    padding: 0;
    overflow-y: auto;
    max-height: 250px;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
    font-size: 14px;
    line-height: 1.42857143;
    color: #333;
 }
 .mlist ul li{
 	text-align: center;
 	display: flex;
 	padding: 10px;
 	border-bottom: 1px solid #ccc;
 }
 .mlist-name{
 	flex: 3;
 	font-size: 18px;
 }
 .mlist-singer{
 	flex: 3;
 	font-size: 14px;
 }
 .inedxNumber{
  font-size: 16px;
  flex: 1;
 }
</style>



