<template>
  <div class="music-play">
  	<div class="music-top"> 
  		<div class="music-left">
  			<div class="music-message">
  				<div class="music-title">{{song.songname}}</div>
  				<div class="music-singer">{{song.singername}}</div>
  			</div>
  			<div class="music-control">
  				<div class="music-player">
  					<span class="icon-previous" @click="prevsong"></span>
  					<span :class="controlClass" @click='switchState'></span>
  					<span class="icon-next" @click='nextsong'></span>
  				</div>
  				<div class="volume-wrapper">
  					<span class="volume icon-volume" @click='showVolumeBar'></span> 
  					<div class="volcontrol" v-show='showlabel'>
  						<div class="volbar">
  							<div class="volbarclick" @click='volvalue'></div>
  							<div class="volbarin" :style="{top: y+'px'}"></div>
  						</div>
  					</div>
  				</div>
  			</div>	
  		</div>

  		<div class="music-right" @click='showLyric'>
  			<div class="music-image" :class="{'music-image-rotate':playing}">
         <img :src="song.albumpic_big">   
        </div>
        <div class="music-lyric"></div>
  		</div>
  	</div>
	 
  	
  	<div class="music-down">
  		<div class="music-time">{{currentTime | timetransform}}/{{duration | timetransform}}</div>
  		<audio :src="song.m4a" autoplay class='music-load' ref='audio' @ended='nextsong' @play='getTime'></audio>
  		<div class="music-len">
  			<span class="music-len-top" :style="{width:x+'px'}"></span>
  			<span class="music-len-bottom" @click='playvalue' ref='musicLen'></span>
  		</div>
  		<div class="music-operating">
  			<span :class="this.classMap[this.order-1]" @click='playorder'></span>
  		</div>
  	</div>
  </div>
</template>

<script>
export default {
  name: 'music-play',
  props:{
  	playList:{
  		type:Array
  	}
  },
  data(){
  	return{
  		currentIndex:0,
  		song:{},
  		controlClass:'icon-play',
  		showlabel:false,
  		x:0,
  		y:0,
  		order:1,
  		classMap:['icon-retweet','icon-random','icon-single'],
  		currentTime:0,
  		duration:0,
      playing:true
  	}
  },
  watch:{
  	'playList':function(){
  		this.song = this.playList[this.currentIndex];
  	},
  	'currentIndex':function(){
  		this.song = this.playList[this.currentIndex];
  	}

  },
  filters:{
  	timetransform(value){
  		var seconds = Math.floor(value%60);
  		var minutes = Math.floor(value/60);
  		return minutes +':'+seconds
  	}
  },
  methods:{
  	switchState(){
  		if(this.controlClass=='icon-play'){
        this.playing = false;
  			this.controlClass='icon-pause';
  			this.$refs.audio.pause();
  		}else{
  			this.controlClass='icon-play';
  			this.$refs.audio.play();
        this.playing = true;
  		}
  		     
   	},
    showLyric(){

    },
   	getTime(){
   		this.duration = this.$refs.audio.duration;
   		this.currentTime = this.$refs.audio.currentTime;
   	},
   	playvalue(event){
   		this.x = event.offsetX;
   		this.$refs.audio.currentTime = this.x/this.$refs.musicLen.clientWidth*this.duration;
   	},
   	volvalue(event){
   		this.y = event.offsetY;
   		var val = (65-this.y)/65;
   		this.$refs.audio.volume = val*val;
   	},
  	prevsong(){
  		if(this.order==1){
  			if (this.currentIndex==0) {
  				this.currentIndex = this.playList.length - 1;
  			}else{
  				--this.currentIndex
  			}	
  		}else if (this.order==2) 
  		{
  			this.currentIndex = Math.floor((this.playList.length) * Math.random());
  		}
      this.currentTime=0;
  	},
  	nextsong(){
  		if(this.order==1){
  			if (this.currentIndex==(this.playList.length - 1)) {
  				this.currentIndex = 0;
  			}else{
  				++this.currentIndex
  			}	
  		}else if (this.order==2) 
  		{
  			this.currentIndex = Math.floor((this.playList.length) * Math.random());
  		}
      this.currentTime=0;
  	},
  	showVolumeBar(){
  		this.showlabel=!this.showlabel;
  	},
    playorder(){
    	if(this.order==3){
    		this.order = 1;
    	}else{
    		++this.order
    	}
    }
  },
  mounted(){
    this.intervalId = setInterval(() => {
      if( this.$refs.audio){
        this.currentTime = this.$refs.audio.currentTime;
      this.x = (this.currentTime/this.duration*this.$refs.musicLen.clientWidth).toFixed(2);
      } 
    },100)
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.music-play{
		width: 80%;
		background: #f5f5f5;
		margin:10px auto;
		box-shadow: 0 1px 8px rgba(0,0,0,0.12);
		border-radius: 8px;
	}
	.music-top{
		display: flex;
	}
	.music-left{
		flex: 1;
	}
	.music-right{
		flex: 1;
    position: relative;
	}
  .music-image{
    width: 200px;
    height: 200px;
    position: absolute;
    top: 0;right: 0;
    bottom: 0;left: 0;
    margin: auto;
    border-radius: 50%;
    animation: circle-rotate 50s infinite linear;
    animation-play-state: paused;
  }
  .music-image img{
    width: 200px;
    height: 200px;
    border-radius: 50%;
  }
  @keyframes circle-rotate{
    0%{
      transform: rotateZ(0deg);
    }
    100%{
      transform: rotateZ(360deg);
    }
  }
  .music-image-rotate{
    animation: circle-rotate 50s infinite linear;
    animation-play-state: running;
  }
	.music-control{
		display: flex;
		justify-content: space-around;

	}
	.music-player{
		flex: 3;
		font-size: 40px;
	}
	.volume-wrapper{
		flex: 1;
		font-size: 20px;
		position: relative;
	}
	.volume{
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -10px;
		margin-top: -10px;
		z-index: 3
	}
	.volcontrol {
		width: 20px;
		height: 100px;
		background-color: #fff;
		position: absolute;
		bottom: calc(50% - 10px);
		left: 50%;
		transform: translateX(-50%);
		z-index: 2;
		border-radius: 4px;
		box-shadow: 0 2px 10px rgba(0,0,0,.12);
	}
	.volbar {
		width: 8px;
		height: 65px;
		background-color: #eee;
		border-radius: 5px;
		position: absolute;
		bottom: 25px;
		left: 6px;
		overflow: hidden;
	}
	.volbarclick {
		position: absolute;
		top: 0;
		left: 0;
		width: 8px;
		height: 65px;
		z-index: 20;
		cursor: pointer;
	}
	.volbarin {
		width: 8px;
		height: 65px;
		background-color: #36DD1B;
		position: relative;
		top: 0;
	}
	.music-message,.music-control{
      	padding: 10px 0 10px 20px;
    }
    .music-title{
    	font-size: 28px;
    	margin:5px 0;
    }
    .music-singer{
    	font-size: 18px;
    	color: #bdbdbd;
    	margin:5px 0;
    }
    .music-control{
    	font-size: 28px;
    }
    .music-down{
		width: 100%;
		padding:  20px;
		display: flex;
    }
    .music-len{
    	position: relative;
    	cursor: pointer;
		width: 70%;
    }
    .music-len-top{
    	position: absolute;
    	top: 0;left: 0;bottom: 0;right: 0;
    	margin: auto 0;
    	width: 0;
    	height: 2px;
    	background-color:#62e572;
    	z-index: 2;
    }
    .music-len-bottom{
    	position: absolute;
    	top: 0;left: 0;bottom: 0;right: 0;
    	margin: auto 0;
    	width: 100%;
    	height: 2px;
    	background: #e5e5e5;
    	background-clip: content-box;
    	padding: 10px 0;
    }
    .music-time{
    	margin-right: 10px;
    	line-height: 40px;
    }
    .music-operating{
    	margin-left: 20px;
    	font-size: 20px;
    }
</style>



