<template>
  <div class="game">
  	<snack1></snack1>	
 	<firgame></firgame>  
  </div>
</template>

<script>
import snack1 from './snack1'
import firgame from './firgame'
export default {
  name: 'game',
  components:{
  	'snack1':snack1,
  	'firgame':firgame
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.game{
		margin:20px auto;
		width: 80%;
	}
	
</style>



