<template>
  <ul class="list">
    <li v-for='item in model'>{{item}}</li>
  </ul>
</template>

<script>
export default {
  name: 'article',
  data(){
  	return{
  		model:['CSS','HTML','JS']
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  li{
  	color: #000;
  }
</style>



