<template>
  <div class="app">
    <aside>
       <v-header></v-header>
       <right-nav></right-nav>
       <v-profie></v-profie>
    </aside>
    <div class='content'>
    <transition name='fade'>
      <router-view></router-view>
    </transition>
    </div>  
    <footer></footer>
  </div>
</template>

<script>
import header from './components/header/header'
import rightnav from './components/rightnav/rightnav'
import profie from './components/profie/profie'

export default {
  name: 'app',
  components: {
    'v-header': header,
    'right-nav': rightnav,
    'v-profie': profie
  }
}
</script>

<style>
.app {
  width: 960px;
  margin: 0 auto;
  display: flex;
  font-family: 'Lato', "PingFang SC", "Microsoft YaHei", sans-serif;
  font-size: 14px;
  line-height: 2;
  padding-left: calc(100vw - 100%);
}
aside{
  color: #fff;
  flex: 0 0 240px;
}
.content{
  flex: 1;
  margin-left: 20px;
  background: #fff;

}
.fade-enter-active, .fade-leave-active {
  transition: opacity 1s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}
</style>



