<template>
  <div class="snackgame">
  		<div class="label-wrapper">
	  		<span class="icon-snack"></span>
	  		<span>贪食蛇</span>
	  	</div>
  		<button @click='isshow=!isshow'>{{isshow ? 'close' : 'open'}}</button>
    	<canvas id='game' v-show='isshow'></canvas>
    	<div class="face">tips: 空格键切换开始与暂停，方向键控制方向</div>
  </div>
</template>

<script>
export default {
  name: 'snackgame',
  data(){
  	return{
  		isshow:false
  	}
  },
  mounted(){
  	var canvas = document.getElementById('game');
		canvas.width = 480
		canvas.height = 480
		var delay =300;
        var gridNum = 24;
		var context = canvas.getContext('2d');
		let cell = [];
		var timer;
			for(let i=0;i<gridNum;i++){
				cell[i]=[];
				for(let j=0;j<gridNum;j++){
					cell[i][j] = 0;  //空白点值为0
				}
			}

		let snackArray =[];
		let foodArray = [];
		let barrierArray = [];
		let snack = {
			direction:'right',
			stop:true
		}

		//找到一个随机空白点
		function findBlank(){
			var i = Math.floor(gridNum*Math.random());
			var j = Math.floor(gridNum*Math.random());
			if(cell[i][j] == 0){
				return [i,j]
			}else{
				return findBlank()
			}
		}


		//初始化 数组位置
		function init(){
			
		//蛇初始位置
			snackArray = [[2,0],[1,0],[0,0]];
			snackArray.forEach(function(item,index){
				cell[item[0]][item[1]] = 3 //蛇点为3
			})
		//随机产生障碍点10个
			for(let i=0;i<10;i++){
				let temp = findBlank();
				barrierArray.push(temp);
				cell[temp[0]][temp[1]] = 2;
			}
		//随机产生食物10个
			for(let i=0;i<10;i++){
				let temp = findBlank();
				foodArray.push(temp);
				cell[temp[0]][temp[1]] = 1;
			}

		} 

		//画出蛇障碍食物
		function draw(arr1,arr2,arr3){
			//化蛇
			let len = arr1.length-1;
			let head_x = arr1[0][0];
			let head_y = arr1[0][1];
			context.fillStyle = '#555';
			context.fillRect(head_x*20,head_y*20,20,20);
			for(let i=1;i<len;i++){
				let o1 = arr1[i][0];
				let o2 = arr1[i][1];
				context.beginPath();
				context.arc(o1*20+10,o2*20+10,10,0,Math.PI*2,true);
				context.closePath();
				context.fillStyle = '#555';
				context.fill();
			}
			let trail_x = arr1[len][0];
			let trail_y = arr1[len][1];
			context.arc(trail_x*20+10,trail_y*20+10,6,0,Math.PI*2,true);
			context.fill();
	  	  //画食物
	  	  let len2 = arr2.length;
	  	  for(let i=0;i<len2;i++){
	  	  	let o1 = arr2[i][0];
	  	  	let o2 = arr2[i][1];
	  	  	context.beginPath();
	  	  	context.arc(o1*20+10,o2*20+10,10,0,Math.PI*2,true);
	  	  	context.closePath();
	  	  	context.fillStyle = '#5CF619';
	  	  	context.fill();
	  	  }
	  	  //画障碍
	  	  let len3 = arr3.length;
	  	  for(let i=0;i<len3;i++){
	  	  	let o1 = arr3[i][0];
	  	  	let o2 = arr3[i][1];
	  	  	context.fillStyle = '#000';
	  	  	context.fillRect(o1*20,o2*20,20,20);
	  	  }
  	    }

  	    document.body.addEventListener('keydown',(event) => {
  	    	event.preventDefault();
  	    	event.returnValue = false;
			switch(event.keyCode){
				case 38:
				if(snack.direction !== 'down'){
					snack.direction = 'up'
				}
				break  
				case 37:
				if(snack.direction !=='right'){
					snack.direction = 'left'
				}
				break
				case 39:
				if(snack.direction !=='left'){
					snack.direction = 'right'
				}
				break
				case 40:
				if(snack.direction !=='up'){
					snack.direction = 'down'
				}
				break
				case 32: //空格键切换暂停开始
				if(snack.stop){
					snack.stop = false;
					setTimeout(gameloop,300);
				}else{
					snack.stop = true;
					clearTimeout(timer);
				}
			}
		})
  	    function gameloop(){
			context.clearRect(0,0,480,480);
			updateData();
			draw(snackArray,foodArray,barrierArray);
			if(snack.stop == false){
				timer =  setTimeout(gameloop, delay);
			}
			
		}
		function removeEl(arr,value){
			for(let i=0;i<arr.length;i++){
				if((arr[i][0] == value[0])&&(arr[i][1] == value[1])){
					arr.splice(i,1);
					break;
				}
			}
		}
		function updateData(){
			//蛇头位置
			let head_x = snackArray[0][0];
			let head_y = snackArray[0][1];
			let leng = snackArray.length;
			//蛇尾位置
			let trail_x = snackArray[leng-1][0];
			let trail_y = snackArray[leng-1][1];
			switch(snack.direction){
				case 'right':
				head_x += 1;
				break;
				case 'up':
				head_y -= 1;
				break;
				case 'down':
				head_y += 1;
				break;
				case 'left':
				head_x -= 1;
			}
			if(cell[head_x][head_y]==2||(head_x>23)||(head_y>23)||(head_x<0)||(head_y<0)){
				alert('撞墙了,gameover');
				snack.stop = true;
			}else if(cell[head_x][head_y]==3){
				alert('撞蛇了');
				snack.stop = true;
			}
			else if(cell[head_x][head_y]==1){
				removeEl(foodArray,[head_x,head_y]);
				snackArray.unshift([head_x,head_y]);
				cell[head_x][head_y] = 3;
				//加一个食物
				let temp1 = findBlank();
				foodArray.push(temp1);
				cell[temp1[0]][temp1[1]] = 1;

			}else{
				snackArray.unshift([head_x,head_y]);
				snackArray.pop();
				cell[head_x][head_y] = 3;
				cell[trail_x][trail_y] = 0;
			}
		}

		init();
		draw(snackArray,foodArray,barrierArray);
  }
}
 </script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.snackgame{
		
	}
	#game{
		margin: 10px auto;
		border:1px solid #000;
		width: 70%;

	}
	.label-wrapper{
	   font-size: 20px;
	   display: inline-block;
	}
	.label-wrapper span{
		vertical-align: middle;

	}
	button{
		margin: 0 0 0 20px;
		outline: none;
		padding: 2px 8px;
		color: #fff;
		cursor: pointer;
		border-radius:5px; 
		background-color: #1AF20C;
		font-size: 18px;
		border-width: 2px;
		vertical-align: middle;
	}
	.face{
		font-size: 12px;
		color: #555;
	}
</style>


