<template>
  <div class="music">
    <music-play :playList='playList'></music-play>
    <music-list @keychange='search' :playList='playList'></music-list>
  </div>
</template>

<script>
import musicPlay from './music-play'
import musicList from './music-list'

export default {
  name: 'music',
  components: {
  	'music-list': musicList,
  	'music-play': musicPlay
  },
  data(){
  	return {
  		urlSearch:'https://route.showapi.com/213-1?showapi_appid=42854&showapi_sign=b0f549e058e74395a979f3b5f06b9888&keyword=',
  		searchText:'陈奕迅',
  		page:1,
  		listLoading:false,
  		playList:[]
  	}
  },
  methods: {
  	search(arg1,arg2){
  		this.listLoading = true;
  		this.$http.get(this.urlSearch + arg1 + '&page=' + arg2).then((res) => {
  			this.playList = res.body.showapi_res_body.pagebean.contentlist;
  		})
	},

  },
  created(){
  	 this.search(this.searchText,this.page);
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.music{
	color: #222;
}

</style>



