<template>
  <div class="rightnav">
     <div class="tab-item"><router-link to="/home">
     	<i class="icon-home"></i>
     	<span class="text">主页</span>
     </router-link></div>
     <div class="tab-item"><router-link to="/game">
     	<i class="icon-game"></i>
     	<span class="text">游戏</span>
     </router-link></div>
     <div class="tab-item"><router-link to="/article">
     	<i class="icon-article"></i>
     	<span class="text">文章</span>
     </router-link></div>
     <div class="tab-item"><router-link to="/music">
     	<i class="icon-music"></i>
     	<span class="text">音乐</span>
     </router-link></div>
  </div>
</template>

<script>
export default {
  name: 'rightnav',
  data (){
  	return{
  	
          }
       }
 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import '../../commom/style.css';

  .rightnav{
  	width: 240px;
  	padding: 10px 0;
  	color: #555;
  	background-color: #fff; 
  }
  .rightnav .tab-item{
  	font-size: 16px;
	position: relative;
  }
  .rightnav .tab-item a{
		display: block;
		text-decoration: none;
		color: rgb(77,85,93);
		padding: 10px 20px;

	}
	.rightnav .tab-item a .text{
		margin-left: 8px;
		vertical-align: top;
	}
	.router-link-active{
		background-color: #f9f9f9;
	}
	.router-link-active :after{
		content: " ";
    	position: absolute;
    	top: 50%;
	    margin-top: -3px;
	    right: 15px;
	    width: 6px;
	    height: 6px;
	    border-radius: 50%;
	    background-color: #bbb;
	}
</style>



