<template>
  <div class="firgame">
  <div class="label-wrapper">
	  		<span class="icon-firgame"></span>
	  		<span>五子棋</span>
	  	</div>
	<button @click='isshow=!isshow'>{{isshow ? 'close' : 'open'}}</button>
    <canvas id='canvas' width='450' height='450'  v-show='isshow'></canvas>
     <div id='messageDiv' v-show='false'></div>
  </div>
 
</template>

<script>
export default {
  name: 'firgame',
   data(){
  	return{
  		isshow:false
  	}
  },
  mounted(){
  	var canvas = document.getElementById("canvas");
  	var button1 = document.getElementById('button1');
  	var messageDiv = document.getElementById('messageDiv');
  	var button2 = document.getElementById('button2');
  	var context = canvas.getContext('2d');
  	var borad = [];
  	var wins = [];
  //赢法数组
  var wins = [];
  for(var i=0;i<15;i++){
  	wins[i] = [];
  	for(var j = 0;j<15;j++){
  		wins[i][j] = [];
  	}
  }
  var count = 0;
  for(var i=0;i<15;i++){
  	for(var j=0;j<11;j++){
  		for(var k=0;k<5;k++){
  			wins[i][j+k][count]=true;
  		}
  		count++;
  	}
  }
  for(var i=0;i<11;i++){
  	for(var j=0;j<15;j++){
  		for(var k=0;k<5;k++){
  			wins[i+k][j][count]=true;
  		}
  		count++;
  	}
  }
  for(var i=0;i<11;i++){
  	for(var j=0;j<11;j++){
  		for(var k=0;k<5;k++){
  			wins[i+k][j+k][count]=true;
  		}
  		count++;
  	}
  }
  for(var i=0;i<11;i++){
  	for(var j=14;j>3;j--){
  		for(var k=0;k<5;k++){
  			wins[i+k][j-k][count]=true;
  		}
  		count++;
  	}
  }
  var myWin = [];
  var comWin = [];
  var result = [];
  var myScore = [];
  var computerScore = [];
  var started;

  function init(){
    //棋盘数组初始化
    for(var i=0;i<=15;i++){
    	borad[i]=[];
    	for(var j=0;j<=15;j++){
    		borad[i][j]=0;
    	}
    }
    result = [0,0,0];
    //画棋盘
    context.strokeStyle = '#000000';
    for(var i=0;i<15;i++){
    	context.beginPath();
    	context.moveTo(15+30*i,15);
    	context.lineTo(15+30*i,435);
    	context.moveTo(15,15+30*i);
    	context.lineTo(435,15+30*i);
    	context.closePath();
    	context.stroke(); 
    }
    for(var i=0;i<count;i++){
    	myWin[i] = 0;
    	comWin[i] = 0;  
    }
    for(var i=0;i<15;i++){
    	myScore[i] = [];
    	computerScore[i] = [];
    	for(var j=0;j<15;j++){
    		myScore[i][j]=0;
    		computerScore[i][j]=0;
    	}
    }
    started = true;
}
function draw(x,y,player){
	if(player==0)
		return;
	context.beginPath();
	context.arc(x*30+15,y*30+15,13,0,Math.PI*2,true);
	context.closePath();
	var g = context.createRadialGradient(x*30+15+2,y*30+15-2,0,x*30+15,y*30+15,15);
	if(player==1){
		g.addColorStop(0,'#636766');
		g.addColorStop(1,'#0a0a0a');
	}else{
		g.addColorStop(0,'#f9f9f9');
		g.addColorStop(1,'#d1d1d1');
	}
	context.fillStyle=g;
	context.fill();
}
  //电脑处理函数
  function computer(data){
  	var x = data[0];
  	var y = data[1];
  	for (var k=0;k<count;k++){
  		if(wins[x][y][k]){
  			myWin[k]++;
  			comWin[k] = -1;
  			if(myWin[k] == 5){
  				result = [15,15,1];
  			}
  		}
  	}
  	var max=0;
  	var u = 0;
  	var v = 0;
  	if(result[2] != 1){
  		for(var i=0;i<15;i++){
  			for(var j=0;j<15;j++){
  				if(borad[i][j] == 0){
  					for(var k=0;k<count;k++){
  						if(wins[i][j][k]){
  							if(myWin[k] == 1){
  								myScore[i][j] += 200;
  							}else if(myWin[k]==2){
  								myScore[i][j] += 400;
  							}else if(myWin[k]==3){
  								myScore[i][j] += 2000;
  							}else if(myWin[k] == 4){
  								myScore[i][j] += 10000;
  							}
  							if(comWin[k] ==1 ){
  								computerScore[i][j]+=220;
  							}else if(comWin[k] == 2){
  								computerScore[i][j]+=420;
  							}else if(comWin[k] == 3){
  								computerScore[i][j]+=2100;
  							}else if(comWin == 4){
  								computerScore[i][j] += 20000;
  							}
  						}
  					}
  					if(myScore[i][j]>max){
  						max = myScore[i][j];
  						u = i;
  						v = j;
  					}else if(myScore[i][j]==max){
  						if(computerScore[i][j]>computerScore[u][v]){
  							u = i;
  							v = j;
  						}
  					}
  					if(computerScore[i][j]>max){
  						max = computerScore[i][j];
  						u =i;
  						v =j;
  					}else if(computerScore[i][j]==max){
  						if(myScore[i][j]>myScore[u][v]){
  							u = i;
  							v = j;
  						}
  					}

  				}
  			}
  		}
  		for (var k=0;k<count;k++){
  			if(wins[u][v][k]){
  				comWin[k] ++;
  				if(comWin[k] == 5){
  					result[2] = 2;
  				}else{
  					result[2] = 0;
  				}
  			}
  		}
  		result[0] = u;
  		result[1] = v;

  	}
  	borad[u][v] = 1;
  	draw(u,v,1);
  	started = true;

  }
  //人落子时间绑定
  canvas.onclick = function(e){
  	if(!started)
  		return;
  	var x = Math.floor(e.offsetX/30);
  	var y = Math.floor(e.offsetY/30);
  	if(borad[x][y] != 0)
  		return;
  	borad[x][y] = 2;
  	draw(x,y,2);
  	started = false;
  	messageDiv.innerHTML = '电脑正在落子...';
  	computer([x,y]);
  }

  init();

}
 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.firgame{
		margin-top: 30px;
	}
	#messageDiv{
	text-align: center;
	font-size: 16px;
	color: #9F9494;
	margin: 0 auto 20px;
}

   .label-wrapper{
	   font-size: 20px;
	   display: inline-block;
	}
	.label-wrapper span{
		vertical-align: middle;

	}
	button{
		margin: 0 0 0 20px;
		outline: none;
		padding: 2px 8px;
		color: #fff;
		cursor: pointer;
		border-radius:5px; 
		background-color: #1AF20C;
		font-size: 18px;
		border-width: 2px;
		vertical-align: middle;
	}
</style>


